.contact.section {
  padding-bottom: 6.25rem;
}

.contact_container {
  grid-template-columns: 4fr 8fr;
  column-gap: 1.875rem;
  height: 400px;
}

p {
  color: #25262c;
  text-align: left;
}

.contact_title {
  font-size: var(--h3-font-size);
  margin-bottom: 0.5rem;
}

.contact_form-group {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 1.5rem;
}

.contact_form-div {
  position: relative;
  margin-bottom: 1.875rem;
  height: 3.75rem;
}

button {
  margin-top: 100px;
}

input::placeholder,
textarea::placeholder {
  color: var(--first-color);
}

.contact_form-input {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  box-shadow: var(--shadow);
  background-color: var(--container-color);
  color: var(--text-color);
  border: none;
  outline: none;
  border-radius: 1rem;
  padding: 0.625rem 1.875rem;
}

/* .contact_form-area {
  resize: none;
} */

.contact_form-area {
  height: 10.25rem;
}

textarea {
  resize: none;
}

@media screen and (max-width: 1024px) {
  .contact_container {
    grid-template-columns: 300px 360px;
    justify-content: center;
    height: 500px;
  }
  .contact_form-group {
    grid-template-columns: 1fr;
  }
  p {
    text-align: justify;
  }
}

@media screen and (max-width: 768px) {
  .contact_container {
    grid-template-columns: 310px;
    row-gap: 1.875rem;
  }
  .contact_info {
    text-align: center;
  }
}

@media screen and (max-width: 350px) {
  .contact_container {
    grid-template-columns: 1fr;
  }
}
